.main-weather-result
  width: 100%
  min-height: 30vh
  display: grid
  align-items: center
  margin-top: $padding__m

  &__wrap
    height: 100%
    display: grid
    grid-template-columns: 1fr 1fr
    grid-template-rows: 1fr auto
    align-items: center
    justify-items: center
    z-index: 1

  &__text
    width: 100%
    display: grid
    justify-items: center
    align-self: start
    position: relative
    padding-top: $padding__s

  &__temperature
    width: 100%
    font-size: 5rem
    font-weight: 300
    margin-block: $padding__xxxs
    padding-left: 1.5rem
    line-height: 100%
    box-sizing: border-box
    display: flex
    justify-content: center

    &::after
      content: "º"
      display: block
      font-size: .75em
      line-height: 1

  &__state
    text-transform: capitalize
    margin: 0
    box-sizing: border-box

  &__state-info
    display: grid
    grid-template-columns: auto auto
    justify-content: end
    margin: 0 auto
    border-radius: 1rem
    padding:
      top: $padding__xxxs
      bottom: $padding__xxxs
    column-gap: $gap__m

  &__state-info > p
    margin: 0
    @include flex(center, center, row)

    & > i
      font-size: inherit
      margin-right: .25rem

  &__image
    max-width: 65%
    @include flex(center,center,row)
    align-self: start
    padding-top: $padding__s

  &__alert
    width: 100%
    opacity: .8
    grid-column: 1/3
    align-self: end

    &-body
      line-height: 1rem
      margin:
        top: $padding__xxs
        bottom: 0

      & > i
        font-size: .8rem

    &-source
      font-style: italic
      margin:
        top: $padding__xxxs
        bottom: 0
